@import "./src/assets/sass/prepends.scss";

main {
  position: relative;
  background-color: $bg-light;
  > article {
    color: $text-black;
  }
}
nav {
  &.nav-transition-enter-active,
  &.nav-transition-leave-active {
    transition: opacity 0.25s;
  }
  &.nav-transition-enter-from,
  &.nav-transition-leave-to {
    opacity: 0;
  }
}
